import React, { Component } from 'react';
import './index.scss';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import { Icon } from 'semantic-ui-react';
import { Link } from 'react-router-dom';
import { URLS } from '../../../utils';
import { DateInput } from 'semantic-ui-calendar-react';

class NewGoal extends Component {
  state = {
      name: "",
      visibility: true,
      stage: 1,
      balance: 600000,
      cost_month: 1,
      date_goal: ''

  }

  

  openChoice = (index) =>{
    switch(index){
        case 1: this.setState({ stage: 3}); break
        case 2 : this.setState({ stage: 4}); break
        case 3 : this.setState({ stage: 5}); break
        default : this.setState({ stage: 1})
    }
  }

  closeForm = () => {
      this.setState({
          visibility: false
      })

      this.props.onClose()
  }


  /*-----ONCHANGES-----------------*/

  nameOnchange = (e) => {
    this.setState({
        name: e.target.value
    })
  }

  cost_monthOnchange = (e) => {
        this.setState({
            cost_month: e.target.value
        })
    }

  date_goalOnchange = (e, {value}) => {
    this.setState({
        date_goal: value
    })
    console.log(this.state.date_goal)
  }

  costOnchange = (e) => {
      console.log(e.target.value)
  }
 

  /*---------------------------------*/

  Submit = () => {
    console.log(this.state.name)
  }


  formSlide = () => {
    switch(this.state.stage){
        case 1: this.setState({stage: 2})
        break
        case 2: this.setState({stage: 3})
        break
        default: this.setState({stage: 1})
        break
    }
  }
   
  cost_month = (<input className = "cost_month" onChange={ this.cost_monthOnchange } placeholder = "Ежемесячная выплата" />)
  submit = (<Link className="new-goal-button acc" to={URLS.GOALS}>Отправить</Link>)  

  render(){
        switch(this.state.stage){
            case 1: 
                return (
                    <ReactCSSTransitionGroup
                    transitionName="example"
                    transitionEnterTimeout={520}
                    transitionLeaveTimeout={520}>
                        <div className="keyholder" key='2'>
                            <Link className="back" to={URLS.GOALS} ></Link>
                            <h1 className="label">Создание цели</h1>
                            <div className ="form">
                                <div className = "title"></div>
                                <div className = "inputs_holder">
                                    <img className="rocket" src={ require('../../../assets/roketa.png') } alt="rocket" />
                                    <div className="field">
                                        <p className="label">Название</p>
                                        <input type = "text" onChange={ this.nameOnchange } className ="goal_name" placeholder=""></input>
                                    </div>
                                    <div className="field">
                                        <p className="label">Необходимая сумма</p>
                                        <input type = "text" onChange={ this.costOnchange } className ="cost" placeholder=""></input>
                                    </div>
                                    <div className="field"> 
                                        <p className="label">Комментарии</p>
                                        <input type = "text" onChange={ this.nameOnchange } className ="goal_link" placeholder=""></input>
                                    </div>
                            </div>
                            <div className="new-goal-button" onClick={this.formSlide}>Далее</div>
                            </div>
                        </div>
                    </ReactCSSTransitionGroup>
                )
            case 2:
                return (
                    <ReactCSSTransitionGroup
                    transitionName="example"
                    transitionEnterTimeout={520}
                    transitionLeaveTimeout={520}>
                    <div className="tiles-keyholder" key='1'>
                        <Link className="back" to={URLS.GOALS} ></Link>
                        <h1 className="label">Методы достижения цели</h1>
                        <div className ="tiles-container">
                            <div className = "tiles">
                                <div className = "credit method-tile" onClick= {this.openChoice.bind(this, 1)}>
                                    <div className="content">
                                        <div className="left">
                                            <Icon className="icon percent" name="percent" />
                                            <div className="credit_title">Кредит</div>
                                        </div>
                                        <div className="text">Зачем ждать? Достигните своей цели прямо сейчас, либо ускорьте её достижение за счёт получения части необходимых средств в виде кредита.</div>                                  
                                    </div> 
                                </div>

                                <div className = "vklad method-tile" onClick= {this.openChoice.bind(this, 2)}>
                                    <div className="content">
                                        <div className="left">
                                            <Icon className="icon" name="chart line" />
                                            <div className="vklad_title">Вклад</div>
                                        </div> 
                                        <div className="text">Вложите деньги на выгодных условиях, чтобы ускорить процесс достижения цели, а также обезопасить свои финансы от нерациональных трат.</div>               
                                    </div>
                                </div>

                                <div className = "nakop method-tile" onClick= {this.openChoice.bind(this, 3)}>
                                    <div className="content">
                                        <div className="left">
                                            <Icon className="icon" name="money" />
                                            <div className="nakop_title">Накопления</div>
                                        </div> 
                                        <div className="text">Откройте обычный "счёт-копилку" без привилегий других наших предложений</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    </ReactCSSTransitionGroup>
                )
            case 3:
                return (
                    <ReactCSSTransitionGroup
                    transitionName="example"
                    transitionEnterTimeout={520}
                    transitionLeaveTimeout={520}>
                    <div  className="keyholder" key='3'>
                        <Link className="back" to={URLS.GOALS} ></Link>
                        <h1 className="label">Кредит</h1>   
                        <div className ="form">                            
                            <div className = "inputs_holder">
                                <img className="target" src={ require('../../../assets/aim.png') } alt="target" />
                                <div className="field">
                                    <p className="label">Выплаты в месяц</p>
                                    {this.cost_month}
               
                                    <div className="progress">
                                        <div style={{width: Math.round((this.state.cost_month/this.state.balance)*100)+"%" }}></div>
                                    </div>
                                    <p className = "indicator">{Math.round((this.state.cost_month/this.state.balance)*100)+"%"} от "свободных" средств</p>
                                </div>  

                                <div className="field">
                                    <p className="label">Дата окончания:</p>
                                    <DateInput className="goal_date" onChange={this.date_goalOnchange} value={this.state.date_goal} />
                                </div>
                                <div className="personal">Согласие на обработку<input type="checkbox"></input></div>
                            </div>
                            {this.submit}
                        </div>
                    </div>    
                    </ReactCSSTransitionGroup>
                )
            case 4:
                return (
                    <ReactCSSTransitionGroup
                    transitionName="example"
                    transitionEnterTimeout={520}
                    transitionLeaveTimeout={520}>
                    <div className="keyholder" key='5'>
                        <Link className="back" to={URLS.GOALS} ></Link>
                        <h1 className="label">Вклад</h1>   
                        <div className ="form">
                            <div className = "inputs_holder">
                                <img className="target" src={ require('../../../assets/graf.png') } alt="target" />
                                <div className="field">
                                    <p className="label">Выплаты в месяц</p>
                                    {this.cost_month}
                             
                                    <div className="progress">
                                        <div background="red" style={{width: Math.round((this.state.cost_month/this.state.balance)*100)+"%" }}></div>
                                    </div>
                                    <p className = "indicator">{Math.round((this.state.cost_month/this.state.balance)*100)+"%"} от "свободных" средств</p>
                                </div>
                                <div className="field">
                                    <p className="label">Дата окончания:</p>
                                    <DateInput className="goal_date" onChange={this.date_goalOnchange} value={this.state.date_goal} />
                                </div>
                                <div className="personal">Согласие на обработку<input type="checkbox"></input></div>
                            </div>
                            {this.submit}
                        </div>
                    </div>   
                    </ReactCSSTransitionGroup> 
                )
            case 5:
                return (
                    <ReactCSSTransitionGroup
                    transitionName="example"
                    transitionEnterTimeout={520}
                    transitionLeaveTimeout={520}>
                    <div className="keyholder" key = '7'>
                        <Link className="back" to={URLS.GOALS} ></Link>
                        <h1 className="label">Накопления</h1>   
                        <div className ="form">
                            <div className = "inputs_holder">
                                <div className = "installment method-tile" onClick= {this.openChoice.bind(this, 1)}>
                                    <div className="content">
                                        <div className="left">
                                            <Icon className="icon percent" name="percent" />
                                            <div className="credit_title">Рассрочка</div>
                                        </div>
                                        <div className="text">Достигните цели здесь и сейчас, а платите потом.</div>                                  
                                    </div> 
                                </div>
                                <div className="field">
                                    <p className="label">Выплаты в месяц</p>
                                    {this.cost_month}
                                </div>
                                <div className="field">
                                    <p className="label">Полная стоимость</p>
                                    <input type = "text" onChange={ this.costOnchange } className ="cost" placeholder=""></input>
                                </div>
                                <div className="field">
                                    <p className="label">Дата окончания</p>
                                    <DateInput className="goal_date" onChange={this.date_goalOnchange} value={this.state.date_goal} />
                                </div>
                            </div>
                            {this.submit}
                        </div>
                    </div>    
                    </ReactCSSTransitionGroup>
                )        
            default:
                return( null )
        }
    }
  
}

export default NewGoal;